import React, { Component } from 'react';
import { IMG_URL } from '../../model';

export default class LandingStep extends Component {
  static get ITEMS() {
    return [
      {
        title: '1.  / ',
        text: '   /     .',
        src: 'img-step-1.svg',
      }, {
        title: '2.   ',
        text: '  .   .',
        src: 'img-step-2.svg',
      }, {
        title: '3.    ',
        text: '       .',
        src: 'img-step-3.svg',
      }, {
        title: '4.    ',
        text: '    ,     .',
        src: 'img-step-4.svg',
      }, {
        title: '5.  ',
        text: '   ,      .',
        src: 'img-step-5.svg',
      }, {
        title: '6.  ',
        text: '     ,     .',
        src: 'img-step-6.svg',
      },
    ];
  }

  render() {
    return (
    <div className='themoin-landing-step'>
      {LandingStep.ITEMS.map((item, index) => (
        <div key={ `step-${index}` }>
          <img src={ `${ IMG_URL }/${ item.src }` } />
          <div>{ item.title }</div>
          <p>{ item.text }</p>
        </div>
      ))}
    </div>
    );
  }
}


