import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { pushView, updateRemitComponent, updateRemitRecipient } from '../action';
import { IMG_URL } from '../model';

class RemitSuccess extends Component {
  constructor(props) {
    super(props);

    const {unit, sendAmount } = props.component;
    this.state = { unit, sendAmount };
  }

  componentDidMount() {
    //facebook event
    fbq('track', 'RemitSuccess', this.props.component);

    this.props.dispatch(updateRemitRecipient({}));
    this.props.dispatch(updateRemitComponent({
      unit: this.props.component.unit,
      sendAmount: '1,000,000',
      receiveAmount: '0',
      couponCode: '',
      couponAmount: 0,
    }));

    //10ping CPA code
    const apiRequestUrl = _tp_GetApiRequestUrl('2017032717330001');
    if (apiRequestUrl !== '') {
      document.write(`<img height="1" width="1" alt="" style="display:none" src="${apiRequestUrl}" />`);
    }
  }

  render() {
    const { unit, sendAmount } = this.state;
    const onMain = () => {
      this.props.dispatch(pushView('main'));
    };

    return (
    <div className='themoin-remit-success'>
    <div>
      <img src={ `${ IMG_URL }/img-success.svg` } />
      <h1>  .</h1>
      <p className='intro'>
           .<br />
              30   .<br />
                .<br />
               .<br />
      </p>
      <div className='box'>
        <p> </p>
        <h3>
          { `${sendAmount} KRW` }
        </h3>
        <p className='account'> </p>
        <h3>
               .<br />
          (   30 )
        </h3>
      </div>
      { unit === 'CNY' ?
        <p className='process'>
            <span>/   2~ 24</span><br />
             .     <br />
          <span>' ()'</span><br />
          <span> </span>   .
        </p> :
        <p className='process'>
            <span>  (09:00 - 15:00,    )</span><br />
            48    .<br />
              <br />
          <span> (Coincheck)  </span>    .
        </p> 
      }
      <a onClick={ onMain }></a>
    </div>
    </div>
    );
  }
}

RemitSuccess.propTypes = {
  dispatch: PropTypes.func,
  component: PropTypes.object,
};

RemitSuccess.mapState = (state) => ({
  component: state.remit.component,
});

export default connect(RemitSuccess.mapState)(RemitSuccess);


