import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import classnames from 'classnames';
import RemitComponent from './RemitComponent';
import MoinPopupRenewal from './components/MoinPopupRenewal';
import { IMG_URL } from '../model';

class Landing extends Component {
  constructor(props) {
    super(props);

    this.state = {
      renewalPopupHidden: false,
    }
  }

  static insertNulls(items) {
    return items.reduce((a, b) => a ? a.concat([null, b]) : [b], null);
  }

  static renderPros() {
    const PROS_LIST = [
      {
        title: ' ', faq: ' ? >',
        items: ['  80%', ' .'],
      }, {
        title: '  ', faq: ' ? >',
        items: ['  ', '   .'],
      }, {
        title: ' ', faq: '  ',
        items: ['    ', '    .'],
      }, {
        title: '  ',
        items: ['    ', '  ,   ', '  .'],
      }, {
        title: ' ',
        items: ['  ', '  ,', '   .'],
      },
    ];

    const getPros = (pros, i, className) => (
      <div className='card-pros' key={ `pros-${i}` }>
        <div className='moin-index-box'>
          <h3>{ pros.title }</h3>
          <p>
            {Landing.insertNulls(pros.items).map((item, j) => (
              item === null ?
                <br key={ `pros-${i}-${j}` } /> :
                <span key={ `pros-${i}-${j}` }>{ item }</span>
            ))}
          </p>
          {pros.faq ?
              <a className='underline' href='/faq'>{ pros.faq }</a> :
            null
          }
        </div>
      </div>
    );

    return (
    <div className='pros'>
    <div>
      <h1 className='moin-row'>
          
      </h1>
      <div className='moin-row'>
        {PROS_LIST.map((pros, i) => (
          getPros(pros, i)
        ))}
      </div>
    </div>
    </div>
    );

  }

  renderIntro() {
    return (
    <div className='intro'>
      <div className='desktop'>
        <div className='col-left'>
          <div className='moin-header'>   </div>
          <div className='moin-header'>  .</div>
          <p>
              ,   ?<br/>
                 .<br/>
                  .<br/>
              ,    !
          </p>
        </div>
        <div className='col-right'>
          <img src={ `${ IMG_URL }/img-main.svg` } />
        </div>
      </div>
      <div className='mobile'>
        <div className='mobile-header'>   </div>
        <div className='mobile-header'>  .</div>
      </div>
    </div>
    );
  }

  renderDescription() {
    return (
    <div className='description'>
      <div>
        <div className='moin-row'>
          <div style={{ paddingBottom: '47px', textAlign: 'center' }}>
            <h1>  </h1>
          </div>
        </div>
        <div className='moin-row images'>
          <div>
            <img src={ `${ IMG_URL }/img-process-bank.png` } />
          </div>
          <div>
            <img src={ `${ IMG_URL}/img-process-moin.png` } />
          </div>
        </div>
        <div className='moin-row details'>
          <div className='moin-panel left'
              style={{ textAlign: 'center', paddingTop: '40px' }}>
            <h3>   </h3>
            <p style={{ paddingTop: '14px' }}>
                     
              <br />
              ,     .
            </p>
          </div>
          <div className='moin-arrow' style={{ textAlign: 'center', paddingTop: '76px' }}>
            <img src={ `${ IMG_URL}/img-3-arrow.svg` } />
          </div>
          <div className='moin-panel right' style={{ textAlign: 'center', paddingTop: '40px' }}>
            <h3>  </h3>
            <p style={{ paddingTop: '14px' }}>
                  
              <br />
                  .
            </p>
          </div>
        </div>
      </div>
    </div>
    );
  }

  renderPros() {
    return Landing.renderPros();
  }

  static renderProcess(simple = false) {
    const ITEMS = [
      {
        url: `${ IMG_URL }/img-process-step-1.png`,
        title: '  ',
        contents: ['   /  ', '    .'],
      }, {
        spacer: true,
        url: `${ IMG_URL }/img-1-arrow.svg`,
      },
      {
        url: `${ IMG_URL }/img-process-step-2.png`,
        title: ' ',
        contents: ['     ', '  .'],
      }, {
        spacer: true,
        url: `${ IMG_URL }/img-1-arrow.svg`,
      }, {
        url: `${ IMG_URL }/img-process-step-3.png`,
        title: ' ',
        contents: ['     ', '48   .'],
      },
    ];

    const elements = ITEMS.map((item, i) => (
      <div className={ classnames('index-contents', { 'card-prize': !item.spacer }) }
          key={ `process-${i}` }>
        <div className={ classnames({ 'content-img': !item.spacer, spacer: item.spacer }) }
            style={{ background: `url("${item.url}") center center no-repeat` }}>
        </div>
        { !item.spacer ?
          <div className='index-contents'>
            <h3>{ item.title }</h3>
            <p>
              {Landing.insertNulls(item.contents).map((content, j) => (
                content === null ?
                  <br key={ `process-${i}-${j}` } /> :
                  <span key={ `process-${i}-${j}` }>{ content }</span>
              ))}
            </p>
          </div> : null
        }
      </div>
    ));

    if (simple) return elements;

    return (
    <div className='process'>
    <div>
      <h1 className='moin-row'>
          
      </h1>
      <div className='moin-row'>
        { elements }
      </div>
    </div>
    </div>
    );
  }

  renderProcess() { return Landing.renderProcess(); }
  onRenewalPopupClose() { this.setState({ renewalPopupHidden: true }); }
  onAndroidLinkClick(e) {
    e.preventDefault();
    window.open('http://market.android.com/details?id=com.themoin.remit', 'new');
    return false;
  }

  render() {
    const { loggedIn } = this.props;

    return (
    <div className='themoin-landing'>
      { loggedIn === null ?
        <div className='landing-pending'></div> :
        <MoinPopupRenewal
          hidden={ this.state.renewalPopupHidden }
          closePopup={ this.onRenewalPopupClose.bind(this) }>
        </MoinPopupRenewal>
      }
      { this.renderIntro() }
      <RemitComponent></RemitComponent>
      { this.renderDescription() }
      { this.renderPros() }
      { this.renderProcess() }
      <div className='video'>
        <div>
          <h1 className='moin-row'>   </h1>
          <div className='embed'>
            {/* <iframe *style={{ width: '940px', height: '529px' }} */}
            <iframe
              src='https://www.youtube.com/embed/U6Gti7-jzuM?rel=0&amp;showinfo=0'
              frameBorder='0' allowFullScreen>
            </iframe>  
          </div>
          <p className='link'>
            <a href='https://youtu.be/3X6keWXCtoo' target='new' className='underline'>    ?</a>
          </p>
        </div>
      </div>
      <div className='prize'>
        <div>
          <img src={ `${ IMG_URL }/img-trophy.png` } />
          <p>
            2016 !   
          </p>
          <div className='moin-subtitle color-deepblue'>
             
          </div>
        </div>
      </div>
      <div className='mobileLink'>
        <div>
          <a className='btn-android' onClick={ this.onAndroidLinkClick.bind(this) }>
            <img src={ `${ IMG_URL }/ic-android.svg` } />
            <div className='hd-button'>  </div>
          </a>

          <a className='btn-ios'>
            <img src={ `${ IMG_URL }/ic-apple.svg` } />
            <div className='hd-button'> iOS  ()</div>
          </a>
        </div>
      </div>
      <div className='funds'>
        <div>
          <img src={ `${ IMG_URL }/img-vc.png` } />
        </div>
      </div>
    </div>
    );
  }
}

Landing.propTypes = {
  loggedIn: PropTypes.bool,
};

const mapState = state => ({
  loggedIn: state.loggedIn,
});

Landing = connect(mapState)(Landing);
export default Landing;



