import React from 'react';
import PropTypes from 'prop-types';
import MoinPopup from './MoinPopup';
import { IMG_URL } from '../../model';

export default class MoinPopupPreview extends MoinPopup {
  renderPopup() {
    return(
      <div className='preview-popup'>
        <div className='popup-content'>
          <button type='button' className='popup-close' onClick={ this.closePopup }>
            <img src={ `${ IMG_URL }/btn-close.png` } />
          </button>
          <table style={{textAlign: 'center', verticalAlign: 'middle'}} className="preview-table">
            <tbody><tr style={{height: 120}} className="back-grey preview-table-title">
              <td style={{width: 260, borderRadius: 4}}>
                <h3></h3>
              </td>
              <td style={{width: 200, fontSize: 18}} className="color-deepblue">
                <span id="preview-send-amount" />
                { this.props.preview.moin['send-amount'] }  
              </td>
              <td style={{width: 260, borderRadius: 4}}>
                <h3> </h3>
              </td>
            </tr>
            <tr style={{height: 7}} />
            <tr>
              <td>
                { this.props.preview.unit === 'JPY' ? 100 : 1}<span className="preview-unit">&nbsp;{ this.props.preview.unit }</span>
                &nbsp;=&nbsp;
                <span id="preview-moin-rate" className="color-deepblue">
                  { this.props.preview.unit === 'JPY' ?
                    (this.props.preview.moin.rate * 100).toFixed(2) : this.props.preview.moin.rate.toFixed(2) }
                </span>
                &nbsp;KRW
              </td>
              <td></td>
              <td>
                { this.props.preview.unit === 'JPY' ? 100 : 1}<span className="preview-unit">&nbsp;{ this.props.preview.unit }</span>
                &nbsp;=&nbsp;
                <span id="preview-bank-rate" className="color-deepblue">
                  { this.props.preview.unit === 'JPY' ?
                    (this.props.preview.bank.rate * 100).toFixed(2) : this.props.preview.bank.rate.toFixed(2) }
                </span>
                &nbsp;KRW
              </td>
            </tr>
            <tr>
              <td>
                <span id="preview-moin-fee" className="color-deepblue">
                  { this.props.preview.moin.fee }
                </span>
                KRW
              </td>
              <td> </td>
              <td>
                <span id="preview-bank-fee" className="color-deepblue">
                  { this.props.preview.bank.fee }
                </span>
                KRW
              </td>
            </tr>
            <tr>
              <td>
                <span className="color-deepblue">-</span>
              </td>
              <td></td>
              <td>
                <span id="preview-bank-fee1" className="color-deepblue">
                  { this.props.preview.bank.fee1 }
                </span>
                KRW
              </td>
            </tr>
            <tr>
              <td>
                <span className="color-deepblue">-</span>
              </td>
              <td>,  </td>
              <td>
                <span id="preview-bank-fee2" className="color-deepblue">
                  { this.props.preview.bank.fee2 }
                </span>
                KRW
              </td>
            </tr>
            <tr style={{height: 100}}>
              <td>
                <h3>
                  <span id="preview-moin-receive" className="color-skyblue">
                    { this.props.preview.moin.receive }
                  </span>
                  <span>&nbsp;</span>
                  <span className="preview-unit">{ this.props.preview.unit }</span>
                </h3>
                <br />
                ( =&nbsp;
                <span id="preview-moin-receivekrw">
                  { this.props.preview.moin.final_fee }
                </span>
                 )
              </td>
              <td>  </td>
              <td>
                <h3>
                  <span id="preview-bank-receive" className="color-skyblue">
                    { this.props.preview.bank.receive }
                  </span>
                  <span>&nbsp;</span>
                  <span className="preview-unit">{ this.props.preview.unit }</span>
                </h3>
                <br />
                ( =&nbsp;
                <span id="preview-bank-receivekrw">
                  { this.props.preview.bank.final_fee }
                </span>
                 )
              </td>
            </tr>
            <tr style={{height: 50}} />
            </tbody>
          </table>
        </div>
      </div>
    );
  }
}

MoinPopupPreview.propTypes = {
  preview: PropTypes.object,
};


