import React, { Component } from 'react';
import PropTypes from 'prop-types';

// TODO: remove jquery.ui dependency

export default class RecipientsBankPopup extends Component {
  componentDidMount() {
    this.onUnitChange(this.props);
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.unit === this.props.unit) return;
    this.onUnitChange(nextProps);
  }

  onUnitChange(nextProps) {
    const close = this.close;
    const { unit, onSubmit } = nextProps;
    const reset = () => { $('#bank-popup input').autocomplete('search', ''); };

    //noinspection JSUnusedAssignment
    $('#bank-popup input').autocomplete({
      minLength: 0,
      source: RecipientsBankPopup.BANKS[unit],
      appendTo: '.modal-bank-result',
      focus: function (event, ui) {
        //$(".modal-bank-search input").val(ui.item.eng);
        return false;
      },
      select: function (event, ui) {
        // 
        onSubmit(`${ui.item.kor} (${ui.item.eng})`);
        close(unit);
        return false;
      },
      close: function () {
        reset();
      },
    }).autocomplete("instance")._renderItem = function (ul, item) {
      return $("<li>")
        .data("item.autocomplete", item)
        .append("<div><h3 class='color-deepblue'>" + item.kor + "</h3><p>" + item.eng + "</p></div>")
        .appendTo(ul);
    };
    reset();
  }

  close(unit) {
    $('#bank-popup').modal('hide');
  }

  onClose() {
    const value = $('#bank-popup .bank-input').val();
    this.props.onSubmit(value);
    this.close(this.props.unit);
  }

  render() {
    const onClose = this.onClose.bind(this);
    return (
      <div className='modal fade' id='bank-popup'
        tabIndex='-1' role='dialog' aria-labelledby=' '>
        <div className='modal-dialog' style={{ maxWidth: '460px', width: 'calc(100% - 30px)' }}>
          <div className='modal-content' style={{ maxWidth: '460px', width: '100%' }}>
            <div className='modal-bank-search'>
              <input className='pull-left bank-input' type='text'
                placeholder=' ?' />
              <button type='button' className='pull-right' id='bank-input-btn'
                onClick={onClose}>
                
            </button>
              <div className='clearfix'></div>
            </div>
            <div className='modal-bank-result'></div>
          </div>
        </div>
      </div>
    );
  }
}

RecipientsBankPopup.propTypes = {
  onSubmit: PropTypes.func,
  unit: PropTypes.string,
};

