import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { pushView } from '../action';
import { IMG_URL } from '../model';

class Footer extends Component {
  pushView(view) {
    this.props.dispatch(pushView(view));
  }

  render() {
    const LINKS = [
      { url: 'company', title: ' ' },
      { url: 'info', title: ' ' },
      { url: 'terms', title: '' },
      { url: 'privacy', title: ' ' },
      { url: 'faq', title: 'F A Q' },
    ];

    return (
    <div className='themoin-footer'>
    <div>
      <div className='section top'>
        <div className='contact'>
          <h3><span className='mobile'>070-5089-2017</span></h3>
          <h1 className='desktop' style={{ padding: '17px 0 14px 0' }}>
            070-5089-2017
          </h1>
          <p>
             09 : 30 ~ 18 : 30 (/ )
            <br />
             : 0504-393-9687
            <br />
             : info@themoin.com
            <br />
              : @ MOIN
          </p>
        </div>
        <div className='account'>
          <h3>  <span className='mobile'>1005-203-071520</span></h3>
          <h1 className='desktop'>
            1005-203-071520
          </h1>
          <p>
              : 
            <br />
             : ()
          </p>
        </div>
        <div className='spacer'></div>
        <div className='links'>
          {LINKS.map(link => (
            <a key={ `footer-link-${link.url}` }
                className='caption'
                onClick={ this.pushView.bind(this, link.url) }>
              { link.title }
            </a>
          ))}
        </div>
      </div>
      <div className='section bottom'>
        <div className='company'>
          <img src={ `${ IMG_URL }/img-moin-logo-grey.svg` } />
          <p>
            ()  |   :   |   : 636-81-00400
            <br />
             :    78 22 2 613
            <br />
             2016 MOIN, Inc. All Rights Reserved.
          </p>
        </div>
        <div className='spacer'></div>
        <div className='social'>
          <a href='https://www.facebook.com/moincompany' target='new'>
            <img className='facebook' src={ `${ IMG_URL }/btn-facebook.png` } />
          </a>
          <a href='http://blog.naver.com/moin_company' target='new'>
            <img className='blog' src={ `${ IMG_URL }/btn-blog.png` } />
          </a>
          <a href='http://plus.kakao.com/home/ne7qlhac' target='new'>
            <img className='yellow-id' src={ `${ IMG_URL }/yello-id.png` } />
          </a>
        </div>
      </div>
    </div>
    </div>
    );
  }
}

Footer.propTypes = {
  dispatch: PropTypes.func,
};

Footer = connect(() => ({}))(Footer);
export default Footer;



