import React, { Component } from 'react';
import PropTypes from 'prop-types';
import update from 'react-addons-update';
import { connect } from 'react-redux';
import { pushView } from '../action';
import { Login } from './Login';
import MoinTextInput from './components/MoinTextInput';
import { IMG_URL, checkEmail, join } from '../model';

const KEYS = ['email', 'password', 'repassword'];

class SignUp extends Component {
  constructor() {
    super();

    this.cbMap = new Map(); // checkbox element map

    const validityMap = KEYS.reduce((map, key) => {
      map[key] = false;
      return map;
    }, {});

    this.state = {
      email: '',
      password: '',
      repassword: '',
      emailErrorMessage: null,
      checked: {
        cbAll: false,
        cb1: false,
        cb2: false,
        cb3: false,
        cbOptional: false,
      },
      validityMap,
    };
  }

  async onSignUp(e) {
    e.preventDefault();

    this.onEmailBlur();
    this.passwordInput.onBlur();

    const isValid = KEYS.every(key => this.state.validityMap[key]);
    if (!isValid) return;

    const terms = [
      { id: 'cb1', text: '  .' },
      { id: 'cb2', text: '   .' },
      { id: 'cb3', text: ' 3  .' },
    ];

    for (const term of terms) {
      if (!this.state.checked[term.id]) {
        window.alert(term.text);
        this.cbMap.get(term.id).focus();
        return;
      }
    }

    const result = await join({
      email: this.state.email,
      password: this.state.password,
      marketing: this.state.checked.cbOptional,
    });

    if (result.ret === 'failure') {
      window.alert(result.msg);
    } else {
      fbq('track', 'Signup');
      window.alert('!  !');
      location.href = '/main'; // no rest api for retrieving user profile
    }
  }

  async onEmailBlur() {
    const result = await checkEmail(this.state.email);
    const emailErrorMessage = result.ret === 'failure' ? result.msg : null;

    this.setState({ emailErrorMessage });
  }

  onLoginView() {
    this.props.dispatch(pushView('login'));
  }

  onTextChange(name, e) {
    const { value, valid } = e;
    const state = update(this.state, {
      [name]: { $set: value },
      validityMap: { [name]: { $set: valid }},
    });

    this.setState(state);
  }

  isAllChecked(except = null) {
    return Object.entries(this.state.checked).every(
      ([id, isChecked]) => (id === 'cbAll' || (except && id === except) ? true : isChecked)
    );
  }

  onCheckboxClick(name, e) {
    const updates = { checked: {} };

    if (name === 'cbAll') {
      const allChecked = this.isAllChecked();

      Object.keys(this.state.checked).forEach(id => {
        updates.checked[id] = { $set: !allChecked };
      });
    } else {
      const checked = !this.state.checked[name];
      const allChecked = this.isAllChecked(name);
      updates.checked[name] = { $set: checked };

      if (allChecked) updates.checked.cbAll = { $set: checked };
    }

    const state = update(this.state, updates);
    this.setState(state);
  }

  render() {
    const { email, password, repassword, checked, emailErrorMessage } = this.state;
    const checkboxes = [
      { id: 'cbAll', label: <label className='caption'>   .</label> },
      { id: 'cb1', label: (
        <label className='caption'>
          <a className='underline' href='/terms' target='new'> </a>   ()
        </label>
      )},
      { id: 'cb2', label: (
        <label className='caption'>
          <a className='underline' href='/privacy' target='new'>  </a>  ()
        </label>
      )},
      { id: 'cb3', label: (
        <label className='caption'>
          <a className='underline' href='/privacy' target='new'> 3 </a>   ()
        </label>
      )},
      { id: 'cbOptional', label: (
        <label className='caption'> SMS,     ()</label>
      )},
    ]

    return (
    <div className='themoin-signup'>
    <div>
      <div className='moin-login'>
        <div className='logo'>
          <img src={ `${ IMG_URL }/img-moin-logo.svg` } style={{ width: '170px' }} />
          <p className='modal-text'>  ! MOIN !</p>
        </div>
        <form id='moin-signup' action='/a/v1/member/join' method='post'
          onSubmit={ this.onSignUp.bind(this) }>
          <MoinTextInput className='moin-input-group'
            label=''
            placeholder='youremail@email.com'
            value={ email }
            errorMessage={ emailErrorMessage }
            onBlur={ this.onEmailBlur.bind(this) }
            onChange={ this.onTextChange.bind(this, 'email') }>
          </MoinTextInput>
          <MoinTextInput className='moin-input-group'
            ref={(input) => { this.passwordInput = input; }}
            label=' ( )'
            type='password'
            placeholder='   6~20'
            value={ password }
            postValidators={[ MoinTextInput.PasswordValidator ]}
            onChange={ this.onTextChange.bind(this, 'password') }>
          </MoinTextInput>
          <MoinTextInput className='moin-input-group'
            label='  '
            type='password'
            placeholder='   .'
            value={ repassword }
            postValidators={[
              value => ({
                pass: password === repassword,
                message: '  .',
              }),
            ]}
            onChange={ this.onTextChange.bind(this, 'repassword') }>
          </MoinTextInput>
          <div className='moin-input-checkbox'>
            {checkboxes.map(checkbox => (
              <div key={ checkbox.id } className='checkbox'
                  onClick={ this.onCheckboxClick.bind(this, checkbox.id) }
                  style={{
                    textAlign: 'left',
                    margin: checkbox.id === 'cbAll' ? '45px 0 25px 0' : null,
                  }}>
                <input type='checkbox'
                  ref={(input) => { this.cbMap.set(checkbox.id, input); }}
                  checked={ checked[checkbox.id] } />
                { checkbox.label }
              </div>
            ))}
          </div>
          <button className='modal-btn btn-signup moin-body' type='submit'>
            
          </button>
        </form>
        <p className='or'>or</p>
        <button className='modal-btn btn-facebook moin-body'
            onClick={ Login.onFacebookLogin }>
            
        </button>
        <button className='modal-btn btn-naver moin-body' style={{ marginTop: '12px' }}
            onClick={ Login.onNaverLogin }>
           ID 
        </button>
        <p style={{ margin: '33px auto 0 auto '}}>
          <a className='underline' onClick={ this.onLoginView.bind(this) }>
             ?
          </a>
        </p>
      </div>
    </div>
    </div>
    );
  }
}

SignUp.propTypes = {
  dispatch: PropTypes.func,
};

SignUp = connect(() => ({}))(SignUp);
export default SignUp;



