import React, { PropTypes } from 'react';
import MoinPopup from './MoinPopup';
import { connect } from 'react-redux';

export default class MoinPopupVerify extends MoinPopup {
  constructor(props) {
    super(props);

    console.log(this.props.profile);
  }

  onPopupClick(e) {
    e.stopPropagation();
  }

  renderPopup() {
    return(
      <div className='verify-popup'>
        <div className='popup-content' onClick= { this.onPopupClick.bind() }>
          <button type='button' className='popup-close' onClick={ this.props.closePopup }>
            <img src="/img/btn-close.png" />
          </button>
          <h1 className='color-darkblue'>   </h1>
          <p>
                  
            <span className='color-darkblue'>({this.props.profile.bank} {this.props.profile.account}) </span>
             1 .      
              1
             .
          </p>
          <img src="/img/img-account.svg" class="img-account"/>
          <p>
             MOIN   
            <span className='color-darkblue'> 4  .</span>
          </p>
          <div>
            <input type='text' placeholder='1234'></input>
            <div className='verify-ok'></div>
          </div>
          <p><a onClick={ this.props.closePopup }>  ?</a></p>
        </div>
      </div>
    );
  }
}

MoinPopupVerify.propTypes = {
  profile: React.PropTypes.object,
};


