import React, { Component } from 'react';
import Landing from './Landing';
import { IMG_URL } from '../model';

class Info extends Component {
  render() {
    return (
    <div className='themoin-info'>
    <div className='info-intro'>
      <div>
        <h1>  </h1>
        <p>
                      .<br />
                  <br />
             80%  ,    .<br />
          <br />
             /    .<br />- 2016 12  -
        </p>
        <img src={ `${ IMG_URL }/img-map.png` }></img>
      </div>
    </div>
    { Landing.renderPros() }
    <div className='info-howto'>
      <div>
        <h1>  </h1>
        <div className='images'>
          <div className='step1'></div>
          <div className='step2'></div>
          <div className='step3'></div>
          <div className='step4'></div>
          <div className='step5'></div>
          <div className='step6'></div>
        </div>
      </div>
    </div>
    </div>
    );
  }
}

export default Info;



