import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import classnames from 'classnames';
import { pushView } from '../action';
import { IMG_URL, logout } from '../model';

const isMobile = true, isIOS = false;

class Header extends Component {
  constructor(props) {
    super(props);

    this.state = {
      menuOpen: false,
    };
  }

  mobileLogout() {
    if (isIOS) {
      window.location = 'ios:logout';
    } else {
      JSInterface.logout();
    }
  }

  pushView(view) {
    if (view === 'logout') {
      // location.href='/logout';
      logout();      
      if (isMobile) { this.mobileLogout(); }
      return;
    }

    if (this.state.menuOpen) this.onMenuToggle();

    this.props.dispatch(pushView(view));
  }

  onMenuToggle() {
    this.setState({ menuOpen: !this.state.menuOpen });
  }

  render() {
    const links = this.props.loggedIn ? [
      { url: 'account', title: '  ' },
      { url: 'recipients', title: ' ' },
      { url: 'logout', title: '' },
    ] : [
      { url: 'login', title: '' },
      { url: 'signup', title: '' },
    ];

    const open = this.state.menuOpen;

    return (
    <nav className='themoin-header'>
      <div>
        <a className='logo'
            onClick={ this.pushView.bind(this, this.props.loggedIn ? 'main' : 'landing' )}>
          <img src={ `${ IMG_URL }/img-moin-logo.svg` } />
        </a>
        <div className='spacer'></div>
        <a className={ classnames('menu', { open }) }
            onClick={ this.onMenuToggle.bind(this) }>
          <div className='line1'></div>
          <div className='line2'></div>
        </a>
        <ul className={ classnames({ open }) }>
          {this.props.profile.group_id ? 
            <li className='mobile'>
              <img src={ `${ IMG_URL }/badge.png` }
                srcSet={ `${ IMG_URL }/badge@2x.png 2x, ${ IMG_URL }/badge@3x.png 3x` } />
            </li> : null
          }
          {links.map(link => (
            <li key={ `link-${link.url}` }>
              <a onClick={ this.pushView.bind(this, link.url) }>{ link.title }</a>
            </li>
          ))}
        </ul>
        {this.props.profile.group_id ? 
          <img className='verify'
            src={ `${ IMG_URL }/badge.png` }
            srcSet={ `${ IMG_URL }/badge@2x.png 2x, ${ IMG_URL }/badge@3x.png 3x` } />
          : null
        }
      </div>
    </nav>
    );
  }
}

Header.propTypes = {
  loggedIn: PropTypes.bool,
  dispatch: PropTypes.func,
  profile: PropTypes.object,
};

const mapState = state => ({
  loggedIn: state.loggedIn,
  profile: state.profile,
});

Header = connect(mapState)(Header);
export default Header;



