import React, { Component } from 'react';
import MoinTextInput from '../components/MoinTextInput';
import LandingStep from '../components/LandingStep';
import Landing from '../Landing';
import { IMG_URL, sendVerificationEmail } from '../../model';

export default class EventUnivJPY extends Component {
  constructor(props) {
    super(props);

    this.state = {
      email: '',
    };
  }

  renderTitle() {
    return (
    <div className='section-title'>
      <img src={ `${ IMG_URL }/img-moin-logo.svg` } />
      <h1 className='pre-title'>{ '{      }' }</h1>
      <h1>   !</h1>
    </div>
    );
  }

  renderIntro() {
    return (
    <div className='section-intro'>
    <div>
      <h1>    <br className='mobile-line-break' />  !</h1>
      <p>
            ,     1.3% .<br className='desktop-line-break' />
          ,      !
      </p>
      <div className='intro-vs'>
        <img src={ `${ IMG_URL }/img-fee-moin.png` } />
        <div className='label-moin-normal'>1.5%</div>
        <img src={ `${ IMG_URL }/ic-arrow.svg` } />
        <div className='label-moin-event'>1.3%</div>
        <img src={ `${ IMG_URL }/img-fee-event.png` } />
      </div>
    </div>
    </div>
    );
  }

  renderHowTo() {
    return (
    <div className='section-howto'>
    <div>
      <h1> ?</h1>
      <img className='desktop' src={ `${ IMG_URL }/img-event-step.svg` } />
      <div className='mobile'>
        <img src={ `${ IMG_URL }/img-event-step-1.svg` } />
        <p>       </p>
        <img src={ `${ IMG_URL }/img-event-step-2.svg` } />
        <p>     </p>
        <img src={ `${ IMG_URL }/img-event-step-3.svg` } />
        <p>   ,   !</p>
        <img src={ `${ IMG_URL }/img-event-step-4.svg` } />
        <p> !     !</p>
      </div>
    </div>
    </div>
    );
  }

  onEmailChange(e) {
    const { value } = e;
    this.setState({ email: value });
  }

  async onClickSend(e) {
    e.preventDefault();

    const result = await sendVerificationEmail(this.state.email);
    window.alert(result.msg);
  }

  renderVerifyEmail() {
    return (
    <div className='section-verifyemail'>
    <div>
      <h1>    </h1>
      <form onSubmit = { this.onClickSend.bind(this) }>
        <MoinTextInput className='input-email'
          placeholder='   .'
          value={ this.state.email }
          vToken={ 1 }
          tabIndex={ 0 }
          postValidators={[ MoinTextInput.EmptyValidator ]}
          onChange={ this.onEmailChange.bind(this) }>
        </MoinTextInput>
        <input type='submit' value='  ' />
      </form>
    </div>
    </div>
    );
  }

  renderCompare() {
    return (
    <div className='section-compare'>
      <h1>100     !</h1>
      <img className='desktop' src={ `${ IMG_URL }/table.svg` } />
      <img className='mobile' src={ `${ IMG_URL }/table-mobile.svg` } />
    </div>
    );
  }

  renderFAQ() {
    return (
    <div className='section-faq'>
    <div className='item-container'>
      <div className='item'>
        <h1>  .</h1>
        <p>
                      .
                      80%  ,
             .    /    .
        </p>
      </div>
      <div className='item'>
        <img className='desktop' src={ `${ IMG_URL }/img-3-merits.svg` } />
        <img className='mobile' src={ `${ IMG_URL }/img-3-merits-m.svg` } />
      </div>
    </div>
    <div className='item-container'>
      <div className='item'>
        <h1>   ?</h1>
        <p>
                  (),
            JASSO     ,    ,        .
              6     .
            <span className='blue'>  ,      </span>.
           SWIFT   .
        </p>
      </div>
      <div className='item'>
        <h1>   ?</h1>
        <p>
               SWIFT .
          SWIFT       -  -      .
           <span className='blue'> SWIFT  ,       </span>.
                      .
          ,      100%   .
        </p>
      </div>
    </div>
    </div>
    );
  }

  renderProcess() {
    return (
    <div className='section-process themoin-landing'>
    <div className='process'>
      <h1>    ?</h1>
      <div>
        { Landing.renderProcess(true) }
      </div>
    </div>
    </div>
    );
  }

  renderStep() {
    return (
    <div className='section-step'>
    <div>
      <h1>    </h1>
      <LandingStep></LandingStep>
    </div>
    </div>
    );
  }

  render() {
    return (
    <div className='themoin-event-univ-jpy'>
    { this.renderTitle() }
    { this.renderIntro() }
    { this.renderHowTo() }
    { this.renderVerifyEmail() }
    { this.renderCompare() }
    { this.renderFAQ() }
    { this.renderProcess() }
    { this.renderStep() }
    { this.renderVerifyEmail() }
    </div>
    );
  }
}



